import {
  Component,
  ComponentFactory,
  ComponentFactoryResolver,
  ComponentRef,
  Input,
  OnChanges,
  SimpleChanges,
  Type,
  ViewChild,
  ViewContainerRef
} from '@angular/core';

@Component({
  selector: 'sof-custom-component-wrapper',
  template: ` <template #dynamicComponent></template> `
})
export class CustomComponentWrapperComponent<C> implements OnChanges {
  /**
   * The component we want to render
   */
  @Input() component: Type<C>;

  /**
   * the input data
   */
  @Input() inputData: { [key: string]: any };
  @ViewChild('dynamicComponent', { static: true, read: ViewContainerRef })
  dynamicComponent;
  componentRef: ComponentRef<C>;

  constructor(private resolver: ComponentFactoryResolver) {
    this.createComponent(this.component);
  }

  createComponent(component): void {
    if (component && this.dynamicComponent) {
      this.dynamicComponent.clear();
      const factory: ComponentFactory<C> = this.resolver.resolveComponentFactory(
        component
      );
      this.componentRef = this.dynamicComponent.createComponent(factory);
      if (this.inputData) {
        Object.entries(this.inputData).forEach(
          ([key, val]) => (this.componentRef.instance[key] = val)
        );
      }
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.inputData && this.componentRef && this.inputData) {
      Object.entries(this.inputData).forEach(
        ([key, val]) => (this.componentRef.instance[key] = val)
      );
    }
    if (changes.component) {
      if (this.componentRef) {
        this.componentRef.destroy();
      }
      this.createComponent(this.component);
    }
  }
}
